.wrapper-teams {
  margin: 0 auto;
}

.block {
  margin: 0;
}

.block-team-logo-img {
  object-fit: cover;
}

.block-team {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}

.block-team-position,
.block-team-name,
.block-team-value,
.block-team-rating,
.block-team-goal-per-game,
.block-team-missed-goal-per-game,
.block-team-won-duels-procent,
.block-team-big-chances,
.block-team-big-chances-created,
.block-team-big-chances-missed,
.block-team-shots-on-target,
.block-team-accurateOwnHalfPassesPercentage,
.block-team-accurateOppositionHalfPassesPercentage,
.block-team-avgAge,
.block-team-averageBallPossession,
.block-team-accuratePassesPercentage {
  // width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  // font-weight: 400;
  // font-size: 18px;
}
.block-team-position {
  // width: 30px;
  // height: 30px;
  // border-width: 4px;
  border-style: solid;
  border-radius: 90%;
  margin-right: 5px;
}
.block-team-name {
  // width: 150px;
  // font-size: 18px;
  // font-weight: 700;
  text-align: center;
}
.block-team-rating {
  border-radius: 5px;
  // font-size: 18px;
  // font-weight: 700;
}

.block-team-logo,
.block-team-logo-img {
  // width: 50px;
  // height: 50px;
  // margin: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-block,
.header-block-logo,
.header-block-team,
.header-block-logo {
  // font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  word-break: break-word;
  text-align: center;
  // padding: 3px;
  // font-size: 16px;
}
// .header-block-logo {
//   // background-color: blue;
// }

.explanation-block {
  // width: 80%;
  text-align: center;
  margin: 0 auto;
  font-style: italic;
  // font-size: 13px;
  // font-weight: 100;
  align-items: center;
}
.line-between-blocks {
  //   // width: 100vw;
  //   // @include adaptiv-value(width, 1300, 920, 0);
  margin: 0 auto 0 auto;
}

/* Small devices (phones, 359px and up) */
@media only screen and (min-width: 359px) {
  .block-team-position,
  .block-team-name,
  .block-team-value,
  .block-team-rating,
  .block-team-goal-per-game,
  .block-team-missed-goal-per-game,
  .block-team-won-duels-procent,
  .block-team-big-chances,
  .block-team-big-chances-created,
  .block-team-big-chances-missed,
  .block-team-shots-on-target,
  .block-team-accurateOwnHalfPassesPercentage,
  .block-team-accurateOppositionHalfPassesPercentage,
  .block-team-avgAge,
  .block-team-averageBallPossession,
  .block-team-accuratePassesPercentage {
    width: 40px;
    font-weight: 400;
    font-size: 7px;
  }
  .block-team-position {
    width: 15px;
    height: 15px;
    border-width: 2px;
    padding: 0;
    margin-right: 2px;
  }
  .header-block-position {
    width: 10px;
    height: 10px;
    border-width: 1px;
    // background-color: red;
  }
  .block-team-name {
    width: 60px;
    font-size: 8px;
    font-weight: 700;
  }
  .header-block-team {
    width: 60px;
    font-size: 5px;
    font-weight: 700;
    // background-color: orange;
  }
  .block-team-value {
    font-size: 6px;
  }
  .block-team-rating {
    font-size: 8px;
    font-weight: 700;
  }

  .block-team-logo,
  .block-team-logo-img {
    width: 20px;
    height: 20px;
    // margin: 2px;
  }
  .header-block-logo {
    width: 30px;
    height: auto;
    // background-color: blue;
    // margin: 2px;
  }

  .header-block,
  // .header-block-logo,
  .header-block-team {
    font-weight: 700;
    padding: 3px;
    font-size: 6px;
  }

  .explanation-block {
    width: 80%;
    font-size: 6px;
    font-weight: 100;
  }
  .line-between-blocks {
    // @include adaptiv-value(width, 1300, 920, 0);
    width: 380px;
  }
}

